* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

:root {
    /* Colors */
    --primary-color: #f2b125;
    --background-color: #fafafa;
    --border-color: #eaf2ff;
    --white: #fff;
    --black: #00204c;


    /* Radius & Shadow */
    --radius: 20px 20px 20px 20px;
    --shadow: 6.43px 7.66px 10px 0px rgba(167, 0, 250, 0.1);
    --brw: 1px;
    --sizeRd: 10px 10px 10px 10px;
    --rd: 10px 10px 10px 10px;
    --sizeBrw: 1px;

    /* Transitions */
    --tran-05: all 0.5s ease;
    --tran-03: all 0.3s ease;
    --tran-02: all 0.2s ease;

    /* Fonts */
    --font_0: 'Suez One', serif;
    --font_1: 'Open Sans', sans-serif;
    --font_2: 'Poppins', sans-serif;
    --font_3: 'Play', sans-serif;
    --font_4: 'Secular One', sans-serif;

    /* Font Size */
    --font-size-14: 14px;
    --font-size-15: 15px;
    --font-size-20: 20px;
    --font-size-28: 28px;
    --font-size-44: 44px;
    --font-size-70: 70px;
}

@media(prefers-reduced-motion: reduce) {
    * {
        animation: none !important;
        transition: none !important;
    }
}

@media screen and (max-width: 720px) {
    :root {
        --font-size-14: 10px;
        --font-size-15: 12px;
        --font-size-20: 15px;
        --font-size-28: 18px;
        --font-size-44: 26px;
        --font-size-70: 34px;
    }

}

.reveal {
    position: relative;
    opacity: 1;
}

