body {
    background-color: var(--background-color);
}

html {
    scroll-behavior: smooth;
}

.start {
    text-align: start;
}

.end {
    text-align: end;
}

.back-to-top {
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 1;
    font-size: 18px;
    outline: none;
    background-color: #232133;
    color: white;
    border: 3px solid var(--white);
    cursor: pointer;
    padding: 10px;
    border-radius: 10px;
    transition: all 0.4s ease;
}

.back-to-top:hover {
    border-color: var(--primary-color);
    transform: scale(1.1);
}

.navbar {
    top: 0;
    padding: 40px 50px;
    justify-content: space-between;
    align-items: center;
    display: flex;
}

.color-primary {
    background-color: var(--primary-color);
    border-radius: 50px;
    color: var(--white);
    padding: 15px;
    margin: 0 10px;
}

.logo-panel {
    width: 215px;
    height: 65px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: var(--black);
    border-radius: var(--radius, 5px);
    box-shadow: var(--shadow, 0 1px 4px rgba(0, 0, 0, .6));
    position: relative;
    padding: 20px;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.logo-panel .logo-img img {
    width: 30px;
    user-select: none;
    cursor: pointer;
    overflow: hidden;
}

.logo-panel .logo-text a {
    color: var(--white);
    font-family: var(--font_3);
    font-size: 20px;
    font-weight: bold;
    text-decoration: none;
    cursor: pointer;
    vertical-align: baseline;
}

.nav-menu {
    position: relative;
}

.nav-menu ul {
    list-style: none;
    display: flex;
    justify-content: space-between;
    white-space: nowrap;
}

.nav-menu ul li {
    padding: 0 5px;
}

.nav-menu ul li a {
    text-decoration: none;
    color: var(--black);
    font-family: var(--font_1);
    font-size: var(--font-size-15);
    padding: 0 10px;
    font-weight: bold;
}

.nav-menu ul li a:hover {
    color: var(--primary-color);
}

.nav-menu ul li .active {
    color: var(--primary-color);
}

.navbar .nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    box-sizing: border-box;
    background-color: var(--white);
    border-radius: var(--radius, 5px);
    display: none;
    box-shadow: var(--shadow, 0 1px 4px rgba(0, 0, 0, .6));
}

.navbar .nav-btn a img {
    margin: auto;
    text-align: center;
    width: 100%;
    cursor: pointer;
}

@media screen and (max-width: 720px) {
    .navbar .nav-menu {
        display: none;
    }

    .navbar .nav-btn {
        display: block;
    }

    .navbar {
        padding: 40px 10px;
    }
}

.mobile-menu {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 99;
    top: 0;
    left: 0;
    background-color: #232133;
    overflow-x: hidden;
    transition: 0.5s;
}

.mobile-menu-content {
    position: relative;
    top: 25%;
    width: 100%;
    text-align: center;
    margin-top: 30px;
}

.mobile-menu a {
    padding: 8px;
    text-decoration: none;
    font-size: 36px;
    color: var(--white);
    display: block;
    transition: 0.3s;
    cursor: pointer;
}

.mobile-menu a:hover,
.mobile-menu a:focus {
    color: var(--white);
}

.mobile-menu .closebtn {
    position: absolute;
    top: 20px;
    right: 45px;
    font-size: 60px;
}

@media screen and (max-height: 450px) {
    .mobile-menu a {
        font-size: 20px
    }

    .mobile-menu .closebtn {
        font-size: 40px;
        top: 15px;
        right: 35px;
    }
}

.product {
    width: 280px;
    height: 131px;
    display: flex;
    align-items: center;
    background-color: var(--white);
    border-radius: var(--radius, 5px);
    box-shadow: var(--shadow, 0 1px 4px rgba(0, 0, 0, .6));
    position: relative;
    padding: 20px;
    transition: var(--tran-03);
    margin: 20px 20px;
}

.product .product-img {
    padding: 0 10px;
}

.product .product-img img {
    width: 78px;
    height: 78px;
    user-select: none;
    object-fit: cover;
    cursor: pointer;
    border: 5px solid var(--border-color);
    border-radius: var(--radius, 5px);
    overflow: hidden;
}

.product .product-title a {
    color: var(--black);
    font-family: var(--font_1);
    font-size: var(--font-size-20);
    font-weight: bold;
    text-decoration: none;
    vertical-align: baseline;
}

.product .product-title p {
    color: var(--black);
    font-family: var(--font_1);
    font-size: var(--font-size-15);
    font-weight: normal;
    text-decoration: none;
    cursor: pointer;
    vertical-align: baseline;
}

.home-info {
    margin: auto;
    align-items: center;
    min-height: 200px;
    text-align: center;
    display: block;
}

.home-info h1 {
    color: var(--black);
    font-family: var(--font_1);
    font-size: var(--font-size-70);
    font-weight: bold;
    line-height: 3;
    vertical-align: baseline;
    padding: 0 10px;
}

.home-info p {
    max-width: 689px;
    text-align: center;
    font-size: var(--font-size-28);
    color: var(--black);
    font-family: var(--font_1);
    font-weight: normal;
    margin: auto;
    padding: 0 10px;
}

@media screen and (max-width: 860px) {
    .home-info {
        margin-top: 50px;
    }

    .home-info h1 {
        line-height: 1.5em;
        margin-bottom: 10px;
    }

    .color-primary {
        display: none;
    }
}

.phone-image-div img {
    width: 100%;
    height: auto;
}

.container {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: calc(100px + 5vh);
}


.container-title {
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 50px;
}

.container-title h2 {
    color: var(--black);
    font-family: var(--font_1);
    font-weight: bold;
    font-size: var(--font-size-44);
}

.container-title h2::after {
    display: block;
