    }

    .left-app-panel .bg-phone {
        position: relative;
        margin: auto;
        right: auto;
    }

    .left-app-panel .bg-product {
        margin: auto;
        left: 0px;
    }

    .right-app-panel .bg-phone {
        position: relative;
        margin: auto;
        right: auto;
    }

    .right-app-panel .bg-product {
        margin: auto;
        left: 0px;
    }
}



.sponsors-title {
    display: flex;
    box-sizing: border-box;
    max-width: 100%;
    align-items: center;
    min-width: 0px;
    min-height: 0px;
    flex-direction: column;
    width: 100%;
    text-align: center;
    margin: 15px 0;
}

.dioun {
    width: 10px;
    height: 10px;
    border-radius: 25px;
    background: var(--primary-color);
    transition: width 0.2s ease 0s;
