}

.sponsors .sponsor a img {
    max-width: 150px;
    max-height: 50px;
    transition: all .2s ease-in-out;
    filter: brightness(0) saturate(100%) invert(28%) sepia(7%) saturate(1176%) hue-rotate(207deg) brightness(97%) contrast(87%);
}

.sponsors .sponsor img:hover {
    transform: scale(1.1);
    filter: none;
}

footer {
    background: var(--background-color);
    width: 100%;
    bottom: 0;
    left: 0;
    color: var(--black);
    border-top: 1px solid var(--black);
}

footer::before {
    content: '';
    position: relative;
    left: 0;
    top: 100px;
    height: 1px;
    width: 100%;
    background: var(--black);
}

footer .content {
    max-width: 1250px;
    margin: auto;
    padding: 30px 40px 40px 40px;
}

footer .content .top {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
}

.content .top .logo-details {
    color: var(--black);
    font-size: 30px;
}

.content .top .media-icons {
    display: flex;
    margin-top: 20px;
}

.content .top .media-icons a {
    height: 40px;
    width: 40px;
    margin: 0 8px;
    border-radius: 10px;
    box-sizing: border-box;
    border: 2px solid var(--black);
    text-align: center;
    line-height: 40px;
    color: var(--black);
    font-size: 17px;
    text-decoration: none;
    transition: all 0.4s ease;
}

.content .top .media-icons a:hover {
    transform: scale(1.1);
    border: 2px solid var(--primary-color);
}

footer .bottom-details {
    width: 100%;
    background: var(--background-color);
}

footer .bottom-details .bottom_text {
    max-width: 1250px;
    margin: auto;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
}

.bottom-details .bottom_text span,
.bottom-details .bottom_text a {
    font-size: 14px;
    font-weight: 300;
    color: var(--black);
    opacity: 0.8;
    text-decoration: none;
}

.bottom-details .bottom_text a:hover {
    opacity: 1;
    text-decoration: underline;
}

.bottom-details .bottom_text a {
    margin-right: 10px;
}

@media (max-width: 700px) {
    footer {
        position: relative;
    }

    .content .top .logo-details {
        font-size: 26px;
    }

    .content .top .media-icons a {
        height: 35px;
        width: 35px;
        font-size: 14px;
        line-height: 35px;
    }

    .bottom-details .bottom_text span,
    .bottom-details .bottom_text a {
        font-size: 12px;
    }
}

@media (max-width: 520px) {
    footer::before {
        top: 145px;
    }

    footer .content .top {
        flex-direction: column;
    }

    .content .top .media-icons {
        margin-top: 16px;
    }

    footer .content .link-boxes .input-box {
        width: 100%;
    }
}

.web-project-slider {
    margin: auto;
    max-width: 1320px;
    height: 680px;
    position: relative;
    vertical-align: baseline;
    object-fit: cover;
    margin-bottom: 100px;
    position: relative;
    overflow: hidden;
    margin: auto;
}

.browser-mockup {
    max-width: 1250px;
    height: 550px;
    border-top: 2em solid #232133;
    box-shadow: var(--shadow, 0 1px 4px rgba(0, 0, 0, .6));
    position: relative;
    border-radius: 5px;
}

.browser-mockup:before {
    display: block;
    position: absolute;
    content: '';
    top: -1.25em;
    left: 1em;
    width: 0.5em;
    height: 0.5em;
    border-radius: 50%;
    background-color: #f44;
    box-shadow: 0 0 0 2px #f44, 1.5em 0 0 2px #fb5, 3em 0 0 2px #9b3;
}

.browser-mockup>* {
    display: block;
}

.browser-mockup {
    margin: 2em;
    flex: 1;
    transform: translateZ(0);
}

.browser-mockup .image-content {
    height: 550px;
    background-size: cover;
    border: 5px solid linear-gradient(145deg, rgb(244, 160, 63) 0%, rgb(243, 185, 26) 100%);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    transition: 1s all;
    display: none;
}

.browser-mockup .arrow-left i {
    font-size: 70px;
    margin: auto;
    position: fixed;
    top: 50%;
    cursor: pointer;
}

.browser-mockup .arrow-left i:hover {
    color: var(--primary-color);
}

.browser-mockup .arrow-right i:hover {
    color: var(--primary-color);
}

.browser-mockup .arrow-right i {
    font-size: 70px;
    margin: auto;
    position: fixed;
    top: 50%;
    right: 0;
    cursor: pointer;
}

.browser-mockup .image-content.image-content:hover {
    transition: 5s all;
    background-position: bottom;
}

@media only screen and (max-width: 768px) {

    .browser-mockup .arrow-right i,
    .browser-mockup .arrow-left i {
        font-size: 40px;
    }

}

